/* src/components/Home.css */
.home {
    background-color: #f4f4f4;
    padding: 80px 20px;
    text-align: center;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-content {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
}

.home-title {
    font-size: 2.5em;
    margin-bottom: 10px;
    color: #000dff;
}

.home-description {
    font-size: 1.2em;
    line-height: 1.6;
    color: #333;
}

.home-content:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
    .home-content {
        padding: 20px;
    }

    .home-title {
        font-size: 2em;
    }

    .home-description {
        font-size: 1em;
    }
}
