/* src/components/AboutMe.css */
.about-me-container {
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    background: #f4f4f4;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: fadeIn 1s ease-in-out;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .about-me-content {
    text-align: center;
  }
  
  .about-me-content h1 {
    font-size: 2.5em;
    color: #000dff;
    margin-bottom: 20px;
  }
  
  .about-me-content p {
    font-size: 1.2em;
    line-height: 1.6;
    color: #333;
    margin-bottom: 20px;
  }
  