/* src/components/ContactMe.css */
.contact-me-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding: 20px;
    background-color: #f4f4f4;
}

.contact-info,
.contact-form {
    width: 100%;
    max-width: 800px;
    margin: 20px;
    padding: 30px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-info h2,
.contact-form h2 {
    font-size: 2em;
    color: #000dff;
    margin-bottom: 15px;
    text-align: center;
}

.contact-info p {
    font-size: 1.1em;
    color: #333;
    line-height: 1.6;
    margin-bottom: 10px;
    text-align: center;
}

.social-links {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.social-links a {
    color: #000dff;
    margin: 0 10px;
    transition: all 0.3s ease-in-out;
}

.social-links a:hover {
    color: #333;
}

.contact-form form {
    text-align: center;
}

.form-group {
    margin-bottom: 20px;
    text-align: left;
}

.form-group label {
    display: block;
    font-size: 1.2em;
    margin-bottom: 8px;
    color: #333;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
}

.form-group textarea {
    height: 150px;
    resize: vertical;
}

button[type="submit"] {
    padding: 12px 30px;
    font-size: 1.2em;
    background-color: #000dff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

button[type="submit"]:hover {
    background-color: #333;
}
